import React, { useEffect, useCallback, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useTranslation, Trans } from 'react-i18next';
import usePortal from 'react-useportal';

// Form
import { useForm, Controller } from 'react-hook-form';
import { useStateMachine } from 'little-state-machine';
import { yupResolver } from '@hookform/resolvers';
import * as Yup from 'yup';

// Update Action
import { resetStore, updateAction } from 'utils/wizard';

// Header Control
import useHeaderContext from 'hooks/useHeaderContext';

// Components
import WizardButtons from 'components/WizardButtons';
import Dropdown from 'components/Dropdown';
import Link from 'components/Link';

// Utils
import { scrollToTop } from 'helper/scrollHelper';

// Assets
import HeaderSplash from 'assets/images/baseLogoSplash.png';

// Data
import { languageData } from 'data/lang';

// Styles
import {
  HeaderImageContainer,
  HeaderImage,
  LogoWhiteBG,
  WelcomeContent,
  WelcomeStyledFormAlternative,
  BoldBlackText,
  WelcomeNote,
  BoldBlackTextPrivacy,
} from '../style';

const schema = Yup.object().shape({
  language: Yup.string().required(),
  // accessCode: Yup.string(),
}).defined();

type Step1Type = Yup.InferType<typeof schema>;

const Step1 = (p: Wizard.StepProps) => {
  const { Portal } = usePortal({
    bindTo: document && document.getElementById('wizard-buttons') as HTMLDivElement,
  });
  const resetExecuted = React.useRef(true);

  const { state, actions } = useStateMachine({ update: updateAction(p.storeKey), reset: resetStore() });

  const store = state?.[p.storeKey];

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [activeStep, setActiveStep] = useState(true);
  const {
    setType, setDoGoBack, setLogoSize, setSubtitle,
  } = useHeaderContext();

  const history = useHistory();

  const {
    control,
    formState,
    handleSubmit,
    reset,
    watch,
  } = useForm({
    defaultValues: store,
    resolver: yupResolver(schema),
    mode: 'onChange',
  });

  const { isValid } = formState;

  const onSubmit = async (values: Step1Type) => {
    if (values) {
      actions.update(values);
      if (p.nextStep) {
        setActiveStep(false);
        history.push(p.nextStep);
      }
    }
  };

  const doBack = useCallback(() => {
    if (p.previousStep) {
      setActiveStep(false);
      history.push(p.previousStep);
    } else {
      history.goBack();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    scrollToTop();
    setDoGoBack(() => doBack);
    setLogoSize('regular');
    setType('null');
    setSubtitle('');
  }, [doBack, setDoGoBack, setLogoSize, setType, setSubtitle]);

  useEffect(() => {
    if (resetExecuted.current) {
      resetExecuted.current = false;
      actions.reset({ welcome: {} });
      reset({ patientId: '' });
    }
  }, [actions.reset, actions, reset]);

  const { t, i18n } = useTranslation();

  const lang = watch('language');

  React.useEffect(() => {
    i18n.changeLanguage(lang);
  }, [i18n, lang]);

  return (
    <WelcomeStyledFormAlternative>
      <HeaderImageContainer>
        <HeaderImage
          src={HeaderSplash}
        />
        <LogoWhiteBG className="logoWhiteBg" />
      </HeaderImageContainer>

      {/* Language */}
      <BoldBlackText>
        {t('main:selectYourLanguage', 'Language')}
      </BoldBlackText>

      <Controller
        control={control}
        name="language"
        defaultValue={i18n.language.split('-')[0] || languageData[0].value}
        render={({ onChange, value }) => (
          <Dropdown
            onChange={e => onChange(e.currentTarget.value)}
            value={value}
          >
            {
                languageData.map(({ value: values, label }) => (
                  <option
                    key={values}
                    id={values}
                    value={values}
                  >
                    {label}
                  </option>
                ))
              }
          </Dropdown>
        )}
      />

      <WelcomeContent>
        <WelcomeNote>
          <Trans i18nKey="main:note">
            <strong>Please note:</strong> This form is for data collection only. It will not predict your COVID-19
            status or diagnose any disease, disorder, or other health condition. Virufy is conducting research and
            will use the information you provide for that research only. Virufy will not take place of a doctor and
            would like to remind you it is your responsibility to seek medical advice from your doctor.
          </Trans>
        </WelcomeNote>

        <BoldBlackTextPrivacy>
          <Link to="https://virufy.org/privacy_policy/" target="_blank">
            <Trans i18nKey="main:privacyPolicy">
              By proceeding you accept the terms of our Privacy Policy
            </Trans>
          </Link>
        </BoldBlackTextPrivacy>

        {activeStep && (
          <Portal>
            <WizardButtons
              invert
              leftLabel={t('helpVirufy:nextButton')}
              leftHandler={handleSubmit(onSubmit)}
              leftDisabled={!isValid}
            />
          </Portal>
        )}
      </WelcomeContent>
    </WelcomeStyledFormAlternative>
  );
};

export default React.memo(Step1);
